import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
    padding: 20px;
`;

let Title = styled.h4`
    font-size: 2rem;
    color: ${ props => props.색상 }
`;


function Detail(props) {

    let [alert, alertChange] = useState(true);
    let [inputData, inputDataChange] = useState('');
    let { id } = useParams();    
    let history = useHistory();
    let findProduct = props.shoes.find(x => x.id == id);

    useEffect(() => {

        // axios.get(); //페이지 첫 로드 했을때 Ajax 요청

        let Timer = setTimeout(() => {
            alertChange(false)
        }, 2000);
        console.log('안녕');
        return () => { clearTimeout(Timer) }
    },[]);

    return (
        <div className="container">
            <Box>
                <Title className="red">Detail</Title>
            </Box>

            { inputData }
            <input onChange={ (e)=>{ inputDataChange(e.target.value) } } />

            { 
                alert === true
                ? (<div className="my-alert2">
                        <p>재고가 얼마 남지 않았습니다.</p>
                    </div>)
                : null
            }

            <div className="row">
                <div className="col-md-6">
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{ findProduct.title }</h4>
                    <p>{ findProduct.content }</p>
                    <p>{ findProduct.price }</p>

                    <Info stock={props.stock}></Info>


                    <button className="btn btn-danger" onClick={ ()=>{ props.stockChange([9, 10, 11]) }}>주문하기</button> 
                    <button className="btn btn-danger" onClick={ ()=>{
                        history.goBack();
                    } }>뒤로가기</button> 
                    <button className="btn btn-danger" onClick={ ()=>{
                        history.push('/');
                    } }>뒤로가기</button> 
                </div>
            </div>
        </div>
    )
  }

  function Info(props) {
      return (
          <p>재고: {props.stock[0]}</p>
      )
  }


  export default Detail;